@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    color: #666;
    font-family: 'Fira Sans', sans-serif;
}
.logo {
    position: relative;
    height: 40px;
    top: 10px;
}
main {
    /* background-color: rgba(170, 170, 170, 0.3); */
    background-color: #0A0C2E;
    color: #60D7AC;
    padding: 10vmin;
    box-shadow: 5px 5px 5px;
    border-radius: 50px;
}
h1 {
    padding-bottom: 2vmin;
}
h1 span {
    color: white;
}
.participans {
    padding-bottom: 1vmin;
}

[type=checkbox] {
    margin: 1vmin .5vmin 1vmin 0;
}
#result {
    margin: 2vmin 0 0 5vmin;
}


label {
    display: block;
}
select {
    width: 12vmin;
}
section {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
.groups {
    margin: 2vmin 0 1vmin;
}
button {
    margin-top: 2vmin;
    padding:5px 15px; 
    cursor:pointer;
    border-radius: 5px;
    outline: none;
}
button:hover {
    color: #fff;
    background-color: #60D7AC;
}
p:nth-of-type(odd) {
    background-color: rgba(255, 255, 255, 0.267);
}
p {
    padding: .5vmin 1vmin;
}
.select-css {
    display: block;
    font-size: 16px;
    font-family: sans-serif;
    font-weight: 700;
    color: #444;
    line-height: 1.3;
    padding: .3em 1.4em .2em .8em;
    width: 100%;
    max-width: 100%; 
    box-sizing: border-box;
    margin: 0;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0,0,0,.04);
    border-radius: .5em;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: #fff;
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
      -webkit-gradient(linear, left top, left bottom, from(#ffffff),to(#e5e5e5));
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
      linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%);
    background-repeat: no-repeat, repeat;
    background-position: right .7em top 50%, 0 0;
    background-size: .65em auto, 100%;
}
.select-css::-ms-expand {
    display: none;
}
.select-css:hover {
    border-color: #888;
}
.select-css:focus {
    border-color: #aaa;
    box-shadow: 0 0 1px 3px #60D7AC;
    box-shadow: 0 0 0 3px -moz-mac-focusring;
    color: #222; 
    outline: none;
}
.select-css option {
    font-weight:normal;
}
.rest {
    padding-top: .5vmin;
    color: rgb(0, 195, 255);
}



h6 {
    text-align: center;
}
